.radio-buttons {
  display: flex;
  margin-bottom: 20px;
}

.radio-buttons label {
  margin-right: 20px;
  cursor: pointer;
  position: relative;
  padding-left: 30px;
}

.radio-buttons input[type="radio"] {
  appearance: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  border: 2px solid #c7c7c7;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.radio-buttons input[type="radio"]:checked {
  background-color: rgb(239, 79, 95);
  border: 6px solid rgb(239, 79, 95);
}

.radio-buttons input[type="radio"]:checked::before {
  content: "";
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
