/* src/Footer.css */

.footer {
  background-color: #f8f8f8;
  padding: 20px 0;
  font-family: Arial, sans-serif;
}

.footer-content {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
}

.column {
  flex: 1;
  min-width: 150px;
  margin: 10px;
}

.column h4 {
  font-weight: bold;
  margin-bottom: 10px;
}

.column ul {
  list-style: none;
  padding: 0;
}

.column ul li {
  margin-bottom: 8px;
}

.social-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
}

.social-links img {
  width: 24px;
  height: auto;
  margin: 5px;
}

.social-links .icons {
  display: flex;
  justify-content: space-around;
  width: 150px;
  margin: 10px 0;
}

.social-links .icons i {
  font-size: 20px;
  color: #333;
}

.social-links .store-links img {
  width: 120px;
  height: auto;
  margin: 5px;
}

.footer-bottom {
  background-color: #f1f1f1;
  padding: 10px 0;
  text-align: center;
  font-size: 12px;
  color: #666;
